import React from "react";
import Layout from "./../components/Layout/Layout";

const About = () => {
  return (
    <Layout title={"Sobre Nosotros"}>
      <div className="row contactus ">
        <div className="col-md-6 ">
          <img
            src="/imagenes/about.png"
            alt="contactus"
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-md-4">
          <p className="text-justify mt-2">
          Alfa Design es un negocio en crecimiento dedicado a la venta de artculos artesanales, 
          con un enfoque especial en cuchillos de alta calidad. Nuestro compromiso es ofrecer productos 
          nicos, hechos con dedicacin y precisin, manteniendo siempre la esencia de un pequeo 
          emprendimiento que valora la artesana y la atencin al detalle en cada creacin.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default About;